import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { FaLinkedinIn, FaGithub, FaTwitter, FaMedium } from 'react-icons/fa/';
import { animations } from '../../styles';

const Container = styled('div')`
  display: flex;
  justify-content: center;
  align-items: center;
  animation: ${animations.fadeInUp} 0.6s 0.6s ease both;
`;

const Href = styled('a')`
  cursor: pointer;
  box-shadow: none;
`;

const iconStyles = ({ theme }) => css`
  font-size: 20px;
  color: ${theme.colors.p600};
  margin: 0 10px;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s,
    transform ${theme.transitions.default};
  :hover {
    transform: translate3d(0px, -1px, 0px);
    color: ${theme.colors.p700};
  }
`;

const StyledIcon = styled(({ component, ...props }) =>
  React.cloneElement(component, props)
)`
  ${iconStyles}
`;

function Social({ twitter, github, linkedin, medium }) {
  const icons = [
    { Icon: FaGithub, url: github, name: 'github' },
    { Icon: FaMedium, url: medium, name: 'medium' },
    { Icon: FaTwitter, url: twitter, name: 'twitter' },
    { Icon: FaLinkedinIn, url: linkedin, name: 'linkedin' },
  ];

  return (
    <Container>
      {icons.map(({ Icon, url, name }, index) => (
        <Href
          aria-label={name}
          key={index}
          href={url}
          target="_blank"
          rel="noopener"
        >
          <StyledIcon component={<Icon />} />
        </Href>
      ))}
    </Container>
  );
}

Social.propTypes = {
  twitter: PropTypes.string.isRequired,
  github: PropTypes.string.isRequired,
  linkedin: PropTypes.string.isRequired,
  medium: PropTypes.string.isRequired,
};

export default Social;
